.d3-ramen{display:flex;flex-direction:column;justify-content:center}.d3-ramen h2{display:flex;justify-content:center}.d3-ramen label{position:relative;left:110px;width:140px}.yText{content:"Calories"}.ramen-list{display:flex;justify-content:center;font-size:25px}.sorter{display:flex;cursor:pointer}.d3-exercise{display:flex;flex-direction:column;justify-content:center}.d3-exercise h2{display:flex;justify-content:center}.d3-exercise .e-btn-cntr{display:flex;justify-content:space-around}.xAxis.exercise{font-size:20px}.xAxis.exercise .tick text{transform:rotate(-25);text-anchor:end}.yAxis.exercise{font-size:20px}.bar{fill:orange;mix-blend-mode:multiply}.bar:hover{opacity:0.66;cursor:pointer}.d3-tip{line-height:1;font-weight:bold;padding:10px;color:black;background-color:rgba(0,0,0,0.3);border-radius:2px;pointer-events:none;position:absolute}.d3-tip:after{box-sizing:border-box;display:inline;font-size:10px;width:100%;line-height:1;margin:20px}
